const ariaLabel = { 'aria-label': 'description' }
const Metrics: any = {
  duration: 'Test Run Duration',
  appStartupDuration: 'App Startup Duration',
  skippedFramesCount: 'Skipped Frames Count'
}

interface SidebarProps {
  activeTab: number | null
  setActiveTab: React.Dispatch<React.SetStateAction<number | null>>
}

const typoStyle = {
  fontSize: '0.9rem',
  lineHeight: '1.3'
}

const lineStyle = '1px solid ' + darkBg2
const FirstColWidth = 135

const listItemStyle = {
  paddingRight: 0,
  paddingLeft: 0
}

const CssTextField = withStyles({
  root: {
    '& input': {
      color: defaultBg,
      fontWeight: '100'
    },
    '& label.Mui-focused': {
      color: defaultBg
    },
    // hover border color
    '& .MuiInput-underline:hover:before': {
      borderBottom: '1px solid ' + superLight
    },
    '& .MuiInput-underline:before': {
      borderBottomColor: superLight
    },
    '& .MuiInput-underline:after': {
      borderBottomColor: defaultBg
    }
  }
})(TextField)

const CssSelect = withStyles({
  root: {
    '& input': {
      color: defaultBg,
      fontWeight: '100'
    },
    '& label.Mui-focused': {
      color: defaultBg
    },
    // hover border color
    '& .MuiInput-underline:hover:before': {
      borderBottom: '1px solid ' + superLight
    },
    '& .MuiInput-underline:before': {
      borderBottomColor: superLight
    },
    '& .MuiInput-underline:after': {
      borderBottomColor: defaultBg
    }
  }
})(Select as any)

const renderTestResultRows = (test: Test, index: number, metric: string, maxValue: number) => {
  //test.testRuns.sort((a: any, b: any) => Date.parse(b.date) - Date.parse(a.date))
  const testRuns: TestRun[] = test.testRuns
  return (
    <>
      {testRuns.map((tr: TestRun, index: number) => (
        <TestResultRow key={tr.id} testRun={tr} metric={metric} maxValue={maxValue} />
      ))}
    </>
  )
}
interface TestResultRowProps {
  testRun: TestRun
  metric: string
  maxValue: number
}

const TestResultRow = (props: TestResultRowProps) => {
  const { testRun, metric, maxValue } = props
  const value = testRun[metric]
  let formatedValue, percentage
  if (value) {
    formatedValue = '' + value
    switch (metric) {
      case 'duration':
        formatedValue = value > 1000 ? formatDuration(value / 1000) : value + ' ms'
        break
      case 'appStartupDuration':
        formatedValue = value + ' ms'
        break
    }
    percentage = (100.0 * value) / maxValue
  } else {
    formatedValue = 'N/A'
    percentage = 0
  }

  return (
    <Typography style={{ display: 'flex', flexDirection: 'row' }} variant="body2" component="div">
      <div style={{ width: FirstColWidth }}>{toDateTime(testRun.date)}</div>
      <div style={{ marginRight: 10 }}>{formatedValue}</div>

      <div style={{ display: 'flex', flexGrow: 1, alignItems: 'center' }}>
        <div style={{ height: 3, width: `${percentage}%`, background: testRun.wasSuccessful ? successGradient : errorGradient }}></div>
      </div>
    </Typography>
  )
}
const getMaxValue = (fieldName: string) => {
  var maxValue = Number.MIN_VALUE

  batchRun.tests.forEach((t: any) =>
    t.testRuns.forEach((tr: any) => {
      if (tr[fieldName]) {
        maxValue = Math.max(maxValue, tr[fieldName])
      }
    })
  )

  return maxValue
}

export const Sidebar = ({ activeTab, setActiveTab }: SidebarProps) => {
  const navigate = useNavigate()

  const [values, setValues] = React.useState({ searchKey: '', filter: 'all', currentMetric: 'duration' })
  const [tests, setTests] = React.useState(batchRun.tests)

  var maxMetricValue = getMaxValue(values.currentMetric)
  // active 1st test
  const defaultTest = React.useCallback((tests: Test[]) => {}, [])

  // filter the tests on the basis of search string or select filter
  const search = React.useCallback(() => {
    const { filter } = values

    const searchKey = values.searchKey.toLowerCase()

    if (!searchKey && filter === 'all') {
      setTests(batchRun.tests)
      return
    }

    let filteredTests: any = batchRun.tests

    if (searchKey)
      filteredTests = filteredTests.filter(
        (test: Test) =>
          test?.id?.toLowerCase().includes(searchKey) ||
          test?.title?.toLowerCase().includes(searchKey) ||
          test.devices.some(d => d.name?.toLowerCase().includes(searchKey))
        // test.devices[0]?.name?.toLowerCase().includes(searchKey)
      )

    if (filter && filter !== 'all')
      filteredTests = filteredTests.filter((test: Test) => {
        const lastTestRun = test?.testRuns[test?.testRuns.length - 1]
        const steps = lastTestRun?.stepResults.filter((step: StepResult) => step.success === (filter === 'successful')) // get steps according to selected option

        if (filter === 'failed') return steps.length > 0 // if single step is found means it's failed test
        return steps.length === lastTestRun?.stepResults.length // else all steps true
      })

    setTests(filteredTests)
    defaultTest(filteredTests) // active 1st test by default
  }, [values, defaultTest])

  // when input changed then do search
  const onChange = (e: any) => {
    const { name, value } = e.target
    setValues({ ...values, [name]: value })
  }

  React.useEffect(() => {
    search() // perform search whenever filter OR search input value changed
  }, [values, search])

  // active 1st test by default
  React.useEffect(() => {
    defaultTest(batchRun.tests as any)
  }, [defaultTest])

  const spacing = 30
  const successPercentage = (100.0 * batchRun.stats?.successCount) / batchRun.stats?.totalCount

  return (
    <div
      id="sidebar"
      style={{
        color: 'white',
        flexBasis: activeTab === null ? '100%' : '50%',
        height: '100%'
      }}
    >
      <div
        id="left-col"
        style={{
          width: 230,
          background: darkBg,
          padding: 60,
          flexBasis: 'auto',
          flexGrow: 0,
          overflow: 'auto'
        }}
      >
        <img
          onClick={() => window.open('https://www.repeato.app')}
          src="assets/logo.png"
          alt="logo"
          style={{ maxWidth: 140, marginBottom: 100, cursor: 'pointer' }}
        />
        <div>
          <Typography variant="h3" component="div">
            Batch Run Report:
          </Typography>
          <Typography variant="h1" component="div" style={{ marginBottom: 26 }}>
            {batchRun.title}
          </Typography>

          <div style={{ position: 'relative', marginBottom: spacing }}>
            <svg width="100%" height="100%" viewBox={`3.8 3.8 34.4 34.4`} className="donut">
              <circle className="donut-ring" cx="21" cy="21" r="15.91549430918954" fill="transparent" stroke={errorWarm} strokeWidth="2"></circle>

              <circle
                className="donut-segment"
                cx="21"
                cy="21"
                r="15.91549430918954"
                fill="transparent"
                stroke={success}
                strokeWidth="2.05"
                strokeDasharray={successPercentage + ' ' + (100 - successPercentage)}
                strokeDashoffset={25}
              ></circle>
            </svg>
            <div
              style={{ position: 'absolute', display: 'flex', justifyContent: 'center', alignItems: 'center', width: '100%', height: '100%', top: 0 }}
            >
              <Typography sx={typoStyle} component="div" style={{ width: 'fit-content' }}>
                <div style={{ borderBottom: lineStyle, marginBottom: 4 }}>
                  {`${batchRun.stats?.failCount} failed`}
                  <br />
                  {`${batchRun.stats?.successCount} successful`}
                  <br />
                </div>
                {`${batchRun.tests?.length} total`}
              </Typography>
            </div>
          </div>

          <NameValue name="Batch Run Date:" value={toDateTime(batchRun?.createdOn)} />
          <NameValue name="Duration:" value={batchRun.stats?.duration} />
          <NameValue name="Steps Executed:" value={batchRun.stats?.stepsCount} />
          <NameValue name="Repeato Version:" value={version} />
        </div>
      </div>
      <div
        id="right-col"
        className={activeTab !== null ? 'hideInPrintVersion' : ''}
        style={{
          background: darkBg2,
          flexGrow: 1,
          display: 'flex',
          flexDirection: 'column'
        }}
      >
        <div style={{ display: 'flex', marginBottom: 16, padding: 40, paddingBottom: 10, alignItems: 'center' }}>
          <div style={{ flexBasis: activeTab === null ? '50%' : '100%', display: 'flex' }}>
            {/** @ts-ignore */}
            <CssTextField
              variant="standard"
              style={{ flexBasis: '44%', flexGrow: 1, marginRight: 30 }}
              placeholder="Search (title, id)"
              name="searchKey"
              onChange={onChange}
              inputProps={ariaLabel}
            />
            {/** @ts-ignore */}
            <CssSelect variant="standard" name="filter" onChange={onChange} value={values.filter} style={{ flexGrow: 1, flexBasis: '10%' }}>
              <MenuItem value="all">All</MenuItem>
              <MenuItem value="successful">Successful</MenuItem>
              <MenuItem value="failed">Failed</MenuItem>
            </CssSelect>
          </div>
          {activeTab === null && (
            <>
              <Typography
                component="div"
                variant="body1"
                style={{
                  paddingRight: 10,
                  paddingLeft: 30
                }}
              >
                Performance Metric:
              </Typography>
              {/** @ts-ignore */}
              <CssSelect variant="standard" name="currentMetric" onChange={onChange} value={values.currentMetric}>
                {Object.keys(Metrics).map((metric, index) => (
                  <MenuItem key={metric} value={metric}>
                    {Metrics[metric]}
                  </MenuItem>
                ))}
              </CssSelect>
            </>
          )}
        </div>

        <List disablePadding={true} style={{ overflow: 'auto', height: '100%', padding: 40, paddingTop: 0 }}>
          {tests.map((test: any, index: number) => (
            <ListItem
              button
              divider
              key={test.id + index}
              style={listItemStyle}
              onClick={() => {
                setActiveTab(index)
                navigate(`/test/${index}/${test.id}`)
              }}
            >
              <ListItemIcon sx={{ color: 'white', minWidth: 36 }}>
                <TestIcon test={test} />
              </ListItemIcon>

              <ListItemText primary={`${test.title}`} secondary={`ID: ${formatId(test.id)}`} style={{ flexGrow: 1, width: 0 }} />

              <ArrowForwardIos style={{ opacity: activeTab === index ? 1 : 0 }} />
              {activeTab === null && (
                <div style={{ flex: 1, flexDirection: 'row', flexGrow: 1 }}>
                  <div>{renderTestResultRows(test, index, values.currentMetric, maxMetricValue)}</div>
                </div>
              )}
            </ListItem>
          ))}
        </List>
      </div>
    </div>
  )
}
