import * as React from 'react'
import ListIcon from '@material-ui/icons/List'
import { Avatar } from '@material-ui/core'
import { Test } from '../types'
import { success, bg2, errorWarm } from '../../../styles/colors'
import { BatchRunExportDataDirName } from '../../../config'

interface TestIconProps {
  test: Test
}

export const TestIcon = (props: TestIconProps) => {
  const { test } = props

  const baseSrc = `./${BatchRunExportDataDirName}/${test.id}`
  const avatarStyle = { width: 18, height: 18 }
  const testRun = test?.testRuns[test?.testRuns.length - 1]
  const bg = testRun ? (testRun.wasSuccessful ? success : errorWarm) : bg2
  const stepsWithImage = test.steps.filter(step => Boolean(step.image))
  const icon = <ListIcon />

  let avatarSrc = ''

  if (stepsWithImage.length > 0) {
    const lastStep = stepsWithImage[stepsWithImage.length - 1]
    avatarSrc = `${baseSrc}/${lastStep.originalFrame}`
  }

  const avatar = (
    <Avatar src={avatarSrc} style={avatarStyle}>
      {icon}
    </Avatar>
  )

  return (
    <div className="stack-container">
      <div style={{ borderRadius: '50%', border: `2px solid ${bg}` }}>{avatar}</div>
    </div>
  )
}
