import { Directions } from './constants'

/**
 * @param {*} step
 */
export const firstDragPoint = step => {
  if (step.mouseMoveCoordinates?.length === 0) return null
  if (step.dragType === 'DragAndDrop') return step.mouseMoveCoordinates[0]
  return step.startPositionSource?.point
}

/**
 * @param {*} step
 */
export const lastDragPoint = step => {
  if (step.mouseMoveCoordinates?.length === 0) return null
  if (step.dragType === 'DragAndDrop') return step.mouseMoveCoordinates[step.mouseMoveCoordinates.length - 1]
  return step.endPositionSource?.point
}

/**
 * @param {*} point
 */
export const infoStringY = point => {
  return point ? `${(point.y * 100).toFixed(1)}%` : ''
}

/**
 * @param {*} point
 */
export const infoStringX = point => {
  return point ? `${(point.x * 100).toFixed(1)}%` : ''
}

/**
 * @param {*} point
 */
export const infoString = point => `x/y: ${(point.x * 100).toFixed(1)}% / ${(point.y * 100).toFixed(1)}%`

/**
 * @param {*} step
 */
export const verticalDragDirection = step => {
  // returns values < 0 for up movement and > 0 for down movement
  return lastDragPoint(step) && firstDragPoint(step) ? lastDragPoint(step).y - firstDragPoint(step).y : null
}

/**
 * @param {*} step
 */
export const horizontalDragDirection = step => {
  // returns values < 0 for left movement and > 0 for right movement
  return lastDragPoint(step) && firstDragPoint(step) ? lastDragPoint(step).x - firstDragPoint(step).x : null
}

/**
 * @param {*} step
 */
export const isHorizontalDrag = step => {
  const horizontalDirection = horizontalDragDirection(step)
  const verticalDirection = verticalDragDirection(step)
  return horizontalDirection !== null && verticalDirection !== null ? Math.abs(horizontalDirection) - Math.abs(verticalDirection) > 0 : null
}

/**
 * @param {*} step
 */
export const dragAndDropGuess = step => {
  // is used to check whether this was a horizontal or vertical drag (scroll operation, slider placement) or if it was a drag and drop
  if (step.longClick) return true

  let minX = 1,
    maxX = 0,
    minY = 1,
    maxY = 0

  // @ts-ignore
  step.mouseMoveCoordinates?.forEach(p => {
    if (p.x > maxX) maxX = p.x
    if (p.x < minX) minX = p.x
    if (p.y > maxY) maxY = p.y
    if (p.y < minY) minY = p.y
  })
  const dx = maxX - minX
  const dy = maxY - minY
  const tolerance = 0.1
  // assume that if there was a 10% movement in both x and y diection, it was a drag and drop gesture
  // this is probably not always true, but the user is always free to correct the step by editing it
  return dx > tolerance && dy > tolerance
}

/**
 * @param {*} step
 */
export const dragDirection = step => {
  const isHorizontal = isHorizontalDrag(step)
  // keep in mind: drag direction is oposite to scroll direction
  if (isHorizontal === null) return null
  else if (isHorizontal) {
    const horizontalDirection = horizontalDragDirection(step)
    return horizontalDirection && horizontalDirection < 0 ? Directions.LEFT : Directions.RIGHT
  } else {
    const verticalDirection = verticalDragDirection(step)
    return verticalDirection && verticalDirection < 0 ? Directions.UP : Directions.DOWN
  }
}
