/* eslint-disable jsx-a11y/alt-text */
import { DrawWidget } from './common/FreeDragLine'
import { PositionSpot } from './common/PositionSpot'
import { AtoBline } from './common/AtoBline'

const DragAndDrop = 'DragAndDrop'
export const DrawDragLine = (props: any) => {
  const { src, step } = props

  return (
    <>
      {step.dragType === DragAndDrop && <DrawWidget step={step} src={src} />}
      {step.dragType !== DragAndDrop && (
        <PositionSpot
          style={{ position: 'relative', zIndex: 100, width: 'fit-content', height: 'fit-content' }}
          positionSource={step.endPositionSource}
        >
          <PositionSpot
            style={{ position: 'relative', zIndex: 100, width: 'fit-content', height: 'fit-content' }}
            positionSource={step.startPositionSource}
          >
            <img style={{ maxWidth: '100%', maxHeight: '-webkit-fill-available' }} src={src} />
          </PositionSpot>
          <AtoBline step={step} />
        </PositionSpot>
      )}
    </>
  )
}
