import { matchColor, roiColor } from '../../../styles/colors'
import { Step } from '../types'
import { DrawDragLine } from './DrawDragLine'

const RectView = ({ match, opacity, style }: any) => {
  let renderStyle: any = {
    position: 'absolute',
    fontSize: 9,
    left: `${match?.position?.x * 100}%`,
    top: `${match?.position?.y * 100}%`,
    width: `${match?.size?.width * 100}%`,
    height: `${match?.size?.height * 100}%`,
    opacity: opacity ? opacity : 1,
    border: `2px solid rgb(110,0,255)`,
    outline: `2px solid ${matchColor}`
  }

  if (style) {
    renderStyle = { ...renderStyle, ...style }
  }

  return <div style={renderStyle}>{/*p: {match.probability.toFixed(3)}*/}</div>
}

export const ImageMatchView = (props: any) => {
  const borderRadius = 5
  const { matchIndex, matches, selection, roi, maxWidth, style, src, children, step, ...rest } = props

  return (
    <div
      {...rest}
      style={{
        position: 'relative',
        overflow: 'hidden',
        maxWidth: maxWidth,
        borderRadius,
        ...style
      }}
    >
      {step.type !== 'drag' && <img src={src} width="100%" alt="ImageMatchView" style={{ borderRadius, display: 'block' }} />}
      {step.type === 'drag' && <DrawDragLine step={step as Step} src={src} />}

      {matches && matches.length
        ? matches.map((match: any, index: any) => <RectView key={index} match={match} opacity={index === matchIndex ? 1 : 0.25} />)
        : selection && <RectView rect={selection} />}

      {roi && (
        <RectView
          rect={roi}
          style={{
            color: roiColor
            // border: `1px solid ${successLight}`,
            // outline: '1000px solid #510000b5'
          }}
        />
      )}
      {children}
    </div>
  )
}
