const ScanTextOperators = {
  contains: { id: 'contains', label: 'contain', js: 'scannedText.includes("searchText")' },
  equals: { id: 'equals', label: 'equal', js: 'scannedText === "searchText"' },
  notContains: { id: 'notContains', label: 'not contain', js: 'scannedText.includes("searchText") == false' },
  greaterThan: { id: 'greaterThan', label: 'be greater than', js: 'parseFloat(scannedText) > parseFloat("searchText")' },
  lessThan: { id: 'lessThan', label: 'be less than', js: 'parseFloat(scannedText) < parseFloat("searchText")' }
}

function formatUserfacingStepId(stepId) {
  return stepId.substr(0, 6).toUpperCase()
}

function formatUserfacingTestId(testId) {
  return testId?.substr(0, 6).toUpperCase()
}

/**
 * @param {String} str
 * @param {String} separator
 */
function stringToSlug(str, separator) {
  str = str.trim()
  str = str.toLowerCase()

  // remove accents, swap  for n, etc
  const from = '/_,:;'
  const to = 'aaaaaaeeeeiiiioooouuuunc------'

  for (let i = 0, l = from.length; i < l; i++) {
    str = str.replace(new RegExp(from.charAt(i), 'g'), to.charAt(i))
  }

  return str
    .replace(/[^a-z0-9 -]/g, '') // remove invalid chars
    .replace(/\s+/g, '-') // collapse whitespace and replace by -
    .replace(/-+/g, '-') // collapse dashes
    .replace(/^-+/, '') // trim - from start of text
    .replace(/-+$/, '') // trim - from end of text
    .replace(/-/g, separator)
}
/**
 * @param {number} duration in seconds
 */
function formatDuration(duration) {
  var delta = duration

  // calculate (and subtract) whole days
  var days = Math.floor(delta / 86400)
  delta -= days * 86400

  // calculate (and subtract) whole hours
  var hours = Math.floor(delta / 3600) % 24
  delta -= hours * 3600

  // calculate (and subtract) whole minutes
  var minutes = Math.floor(delta / 60) % 60
  delta -= minutes * 60

  // what's left is seconds
  var seconds = delta % 60

  if (days) {
    days = `${days}d `
  } else {
    days = ''
  }
  return `${days}${min2Digits(hours)}:${min2Digits(minutes)}:${min2Digits(Math.floor(seconds))}`
}

const min2Digits = number => {
  if (number < 10) {
    return '0' + number
  }
  return number
}

/**
 * Convert ms to human readable time
 * @param duration
 * @returns string
 */
const msToTime = duration => {
  if (!duration) return ''
  let seconds = Math.floor((duration / 1000) % 60)
  let minutes = Math.floor((duration / (1000 * 60)) % 60)
  let hours = Math.floor((duration / (1000 * 60 * 60)) % 24)

  if (hours < 10) {
    hours = '0' + hours
  }
  if (minutes < 10) {
    minutes = '0' + minutes
  }
  if (seconds < 10) {
    seconds = '0' + seconds
  }
  return hours + ':' + minutes + ':' + seconds
}

/**
 * Convert string to local date time format
 * @param date
 * @returns
 */
const toDateTime = date => {
  if (!date) return ''

  const dateObj = new Date(date)

  if (isNaN(dateObj.getDate())) return ''

  return dateObj.toLocaleDateString() + ' ' + dateObj.toLocaleTimeString()
}

function getRandomCharacter() {
  const alphabet = 'abcdefghijklmnopqrstuvwxyz'
  const index = Math.floor((alphabet.length - 1) * Math.random())
  return alphabet[index]
}

function trim(string, char) {
  if (char === ']') char = '\\]'
  if (char === '\\') char = '\\\\'
  return string.replace(new RegExp('^[' + char + ']+|[' + char + ']+$', 'g'), '')
}

function formatDateForFilename(date) {
  const dateTimeFormat = new Intl.DateTimeFormat('de', {
    hour: 'numeric',
    minute: 'numeric',
    second: 'numeric',
    year: 'numeric',
    month: 'numeric',
    day: 'numeric'
  })
  const [
    { value: day },
    ,
    { value: month },
    ,
    { value: year },
    ,
    { value: hour },
    ,
    { value: minute },
    ,
    { value: second }
  ] = dateTimeFormat.formatToParts(date)
  const dateTimeStr = `${year}-${month}-${day}-${hour}-${minute}-${second}`
  return dateTimeStr
}

function formatDateTime(date) {
  if (!date) return ''
  return `${date.toLocaleDateString()} ${date.toLocaleTimeString()}`
}

/**
 * Similar to grep:
 * 1. Filter each line by looking for a string.
 * 2. Remove removeString
 * 3. Return first line
 * @param {string} string
 * @param {string} search regex eg. "myVariable=([0-9])"
 * @returns {string}
 */
function filterMultiline(string, search) {
  const lines = string.split('\n')
  const filteredLines = lines.map(line => line.trim()).filter(line => line.length > 0)
  const results = []
  const regex = new RegExp(search)

  filteredLines.forEach(line => {
    const match = line.match(regex)
    if (match) {
      if (match.length > 1) {
        results.push(match[1].replace('\r', '').trim())
      } else {
        results.push(match[0].replace('\r', '').trim())
      }
    }
  })

  if (results.length > 1) {
    console.warn('Multiple lines found:', results)
    return results[0]
  } else if (results.length === 1) {
    return results[0]
  } else {
    return null
  }
}

function truncateStart(string, maxLength) {
  if (string.length > maxLength) {
    string = '...' + string.substring(string.length - maxLength, string.length)
  }
  return string
}

function truncate(string, maxLength) {
  if (string.length > maxLength + 3) {
    string = string.substring(0, maxLength - 3) + '...'
  }

  return string
}

/**
 *
 * @param {string} string
 * @param {Object.<string, string>} replaceDict
 * @returns String
 */
function replaceAllDict(string, replaceDict) {
  for (var key in replaceDict) {
    // @ts-ignore
    string = replaceAll(string, key, replaceDict[key])
  }
  return string
}
/**
 * Replaces all occurenses of a string
 * @param {string} string
 * @param {string} search
 * @param {string} replacement
 */
function replaceAll(string, search, replacement) {
  return string.split(search).join(replacement)
}

function getScanTextLabel(step) {
  if (step.scanTextLabel) return step.scanTextLabel
  // in batchViewer we can't access view properties because we don't have MST models. This is the fallback:
  return ScanTextOperators[step.operator]?.label
}

/**
 * Return step name according to the type of step
 * @param {*} step
 * @param type
 * @returns
 */
const getStepInfo = (step, type = 'all', tagWidget = true) => {
  let start = ''
  let end = ''
  let sDrop = ''
  let iconDrop = null

  const pDrop = step.longClick ? ' and drop' : ''

  if (step.type === 'drag') {
    if (step.stopOnFingerprintFound) {
      sDrop = `until fingerprint is found`
    } else if (isHorizontalDrag(step)) {
      const horizontalDirection = horizontalDragDirection(step)
      iconDrop = horizontalDirection && horizontalDirection > 0 ? ArrowForward : ArrowBack
      start = infoStringX(firstDragPoint(step))
      end = infoStringX(lastDragPoint(step))
      sDrop = `${start} -> ${end}`
    } else {
      const verticalDirection = verticalDragDirection(step)
      iconDrop = verticalDirection && verticalDirection > 0 ? ArrowDownward : ArrowUpward
      start = infoStringY(firstDragPoint(step))
      end = infoStringY(lastDragPoint(step))
      sDrop = `${start} -> ${end}`
    }
  }

  let textLabel

  if (step.type === 'text') {
    const actions = []
    step.clearText && actions.push('Clear text')
    step.text && actions.push('Type')
    textLabel = actions.join(' and ')
  }

  const allSteps = {
    click: {
      icon: TouchApp,
      primary: step.longClick ? 'Long Click' : 'Click',
      secondary: 'on fingerprint',
      cmdLineIcon: ''
    },
    comment: {
      // icon: Comment,
      primary: <b>{step.title}</b>,
      secondary: step.description,
      cmdLineIcon: ''
    },
    drag: {
      icon: iconDrop,
      primary: `Drag ${dragAndDropGuess(step) ? 'around' : dragDirection(step) || ''}${pDrop}`,
      secondary: sDrop,
      cmdLineIcon: ''
    },
    waitForTemplate: {
      icon: HourglassEmpty,
      primary: 'Wait',
      secondary: 'for fingerprint to show',
      cmdLineIcon: ''
    },
    waitForDuration: {
      icon: HourglassEmpty,
      primary: 'Wait',
      secondary: `for ${step.duration} ms ${step.subtype === 'waitForAnimationFinish' ? ' (screen animation)' : ''}`,
      cmdLineIcon: ''
    },
    waitForKeyboard: {
      icon: HourglassEmpty,
      primary: 'Wait',
      secondary: 'for keyboard to show up',
      cmdLineIcon: ''
    },
    text: {
      icon: TextFields,
      primary: textLabel,
      secondary: step.text && `"${step.text}"`,
      cmdLineIcon: ''
    },
    scanText: {
      primary: 'Scan for text',
      secondary: '',
      cmdLineIcon: ''
    },
    home: {
      icon: Home,
      primary: 'Home press',
      secondary: '',
      cmdLineIcon: ''
    },
    back: {
      icon: KeyboardBackspace,
      primary: 'Back press',
      secondary: '',
      cmdLineIcon: ''
    },
    appSwitch: {
      icon: FilterNone,
      primary: 'Show app switch',
      secondary: '',
      cmdLineIcon: ''
    },
    templateAssertion: {
      icon: Search,
      primary: 'Check specific element',
      secondary: '',
      cmdLineIcon: ''
    },
    someContentAssertion: {
      primary: 'Check some dynamic element',
      secondary: '',
      cmdLineIcon: ''
    },
    someContentClick: {
      primary: 'Click',
      secondary: 'on some content at x/y',
      cmdLineIcon: ''
    },
    xyClick: {
      primary: 'Click',
      secondary: `at x/y: ${Math.floor(step.point?.x * 100)}% / ${Math.floor(step.point?.y * 100)}%`,
      cmdLineIcon: ''
    },
    startApp: {
      icon: StartIcon,
      primary: 'Start App',
      secondary: [step.packageName, step.iosBundleId].join(' | '),
      cmdLineIcon: ''
    },
    switchDevice: {
      icon: MobileScreenShare,
      primary: 'Switch Device',
      secondary: [step.roleName, step.deviceName].join(' | '),
      cmdLineIcon: ''
    },
    stopApp: {
      icon: LandingIcon,
      primary: 'Stop App',
      secondary: step.packageName ? step.packageName : step.iosBundleId,
      cmdLineIcon: ''
    },
    execScript: {
      icon: CommandLineIcon,
      primary: step.language === 'cli' ? 'Exec Command Line Script' : 'Exec JavaScript',
      secondary: step.script,
      cmdLineIcon: '>'
    },
    subTest: {
      icon: List,
      primary: 'SubTest',
      secondary: '',
      cmdLineIcon: '>'
    },
    scanTextAssertion: {
      icon: Search,
      primary: 'Check for text',
      secondary: step.assertionJs ? `Check: ${step.assertionJs}` : `Scanned text should ${getScanTextLabel(step)} "${step.searchText}"`,
      cmdLineIcon: ''
    },
    scanTextClick: {
      primary: 'Click on text',
      secondary: `"${step.searchText}"`,
      cmdLineIcon: ''
    },
    scanTextWait: {
      icon: HourglassEmpty,
      primary: 'Wait',
      secondary: step.assertionJs ? `Until true: ${step.assertionJs}` : `Until scanned text ${getScanTextLabel(step)} "${step.searchText}"`,
      cmdLineIcon: ''
    }
  }

  const noDesc = 'No description found for this element'
  const stepText = allSteps[step.type] || {
    primary: noDesc,
    secondary: noDesc,
    icon: TouchApp,
    cmdLineIcon: ''
  }
  if (step.optional) stepText.primary = `Optional ${stepText.primary}`
  if (tagWidget && step.tags) {
    stepText.secondary = (
      <span>
        {stepText.secondary}{' '}
        {step.tags.map(tag => (
          <TagWidget name={tag} key={tag} />
        ))}
      </span>
    )
  }
  return type === 'all' ? stepText : stepText[type]
}

/**
 * Looks for placeholders in str in the form of "$VARNAME" and adds an entry to replaceStrings dictionary:
 *  Key: The $VARNAME variable
 *  Value: The respective env var value from process.env
 * @param {*} str
 * @param {*} replaceStrings
 */
function addReplacementStringsWithEnvVars(str, replaceStrings) {
  const vars = str.match(/\$[a-zA-Z0-9_]+/g)
  vars?.forEach(envVar => {
    if (!replaceStrings[envVar]) {
      replaceStrings[envVar] = process.env[envVar.replace('$', '')]
    }
  })
}

// For some reason we used a single number to version mapping in the iOS connector
// In the long run I'd like to get rid of it.
const protocolToVersionNum = num => {
  const str = '' + num
  return [str[0], str[1], str[2]].join('.')
}

const getPropFromIosPackageInfo = (packageInfo, prop) => {
  /**
   *CFBundleIdentifier = "com.stephanpetzl.film-o-matic";
    CFBundleInfoDictionaryVersion = "6.0";
    CFBundleName = MovieSwift;
    CFBundleVersion = 1400;
    ...
   */
  const regex = new RegExp(`${prop} = (.*);`)
  const matches = regex.exec(packageInfo)
  if (matches && matches.length > 1) {
    return replaceAll(matches[1], '"', '').trim()
  } else return undefined
}

// Unfortunately this never worked in release builds- always reported wrong line numbers
const stacktraceToLineNumber = (stack, offset = -2) => {
  console.info('stacktraceToLineNumber', stack)
  const regex = new RegExp(`([0-9]+:[0-9]+)`)
  const matches = regex.exec(stack)
  if (!matches?.length) {
    return ''
  } else {
    const lineAndCol = matches[1].split(':')
    return `line ${parseInt(lineAndCol[0]) + offset}, column ${lineAndCol[1]}`
  }
}

/**
 * Compare two version strings
 * @param {String} version1
 * @param {String} version2
 * @returns {boolean} true if version1 is greater than version2
 */
const isVersion1GreaterThanVersion2 = (version1, version2) => {
  version1 = version1.replace('v', '')
  version2 = version2.replace('v', '')
  return (
    version1.localeCompare(version2, undefined, {
      numeric: true
    }) > 0
  )
}

