//@ts-check
import React from 'react'
import { Chip, Tooltip, withStyles } from '@material-ui/core'
import { inject, observer } from 'mobx-react'
import { bg2 } from '../../styles/colors'
import compose from '../../utils/compose'

const styles = () => ({
  tag: { height: 20, marginRight: 6 }
})

const enhance = compose(
  // @ts-ignore
  withStyles(styles),
  inject('dbStore'),
  observer
)

/**
 * @typedef Props
 * @prop {string} name
 * @prop {any} classes
 * @prop {any} onClick
 * @prop {any} style
 */

/**
 * @param {Props} props
 */
function TagWidget(props) {
  // @ts-ignore
  const { classes, name, dbStore, fallbackColor = bg2 } = props
  const tag = dbStore.getTagByName(name)
  const style = { backgroundColor: tag?.color || fallbackColor, ...props.style }

  if (props.onClick) style.cursor = 'pointer'

  const chip = <Chip size="small" label={name} className={classes.tag} style={style} onClick={props.onClick} />
  if (tag?.description) return <Tooltip title={tag.description}>{chip}</Tooltip>
  else return chip
}

// @ts-ignore
export default enhance(TagWidget)
