/* eslint-disable jsx-a11y/alt-text */
import { useRef, useState } from 'react'
import { defaultBg, secondary } from '../../../../styles/colors'
import { Step } from '../../types'

interface Props {
  step: Step
  src: string
}

export const DrawWidget = ({ step, src }: Props) => {
  const ref = useRef<any>(null)
  const [size, setSize] = useState({ width: 500, height: 800 })

  const getCoordinate = (cmd: string, point: { x: number; y: number }) => {
    return `${cmd} ${size.width * point.x} ${size.height * point.y} `
  }

  const onImgLoad = (event: any) => {
    const img = event.target
    setSize({ width: img.offsetWidth, height: img.offsetHeight })
  }

  const points = step.mouseMoveCoordinates

  let path = ''
  let arrowTransform = ''
  let longClickDot = null
  let longClickDotBg = null

  if (points.length > 1) {
    path += getCoordinate('M', points[0])
    for (let i = 1; i < points.length; i++) {
      path += getCoordinate('L', points[i])
    }
    const firstPoint = points[0]
    const lastPoint = points[points.length - 1]
    let oneBefore = points[points.length - 2]

    const dx = lastPoint.x - oneBefore.x
    const dy = lastPoint.y - oneBefore.y
    const deg = (Math.atan2(dy, dx) * 180) / Math.PI
    arrowTransform = `translate(${size.width * lastPoint.x} ${size.height * lastPoint.y}) rotate(${deg} 0 0) translate(-2 -7)`

    if (step.longClick) {
      longClickDotBg = (
        <circle cx={size.width * firstPoint.x} cy={size.height * firstPoint.y} r={10} stroke={defaultBg} fill={defaultBg} strokeWidth="10" />
      )
      longClickDot = (
        <circle cx={size.width * firstPoint.x} cy={size.height * firstPoint.y} r={10} stroke={secondary} fill={secondary} strokeWidth="2" />
      )
    }
  }

  return (
    <div ref={ref} style={{ position: 'relative', width: 'fit-content', height: 'fit-content', cursor: 'crosshair' }}>
      <img style={{ maxWidth: '100%', maxHeight: '-webkit-fill-available' }} src={src} onLoad={onImgLoad} />
      <svg
        style={{ position: 'absolute', top: 0, left: 0, width: '100%', height: '100%' }}
        xmlns="https://www.w3.org/2000/svg"
        viewBox={`0 0 ${size.width} ${size.height}`}
        preserveAspectRatio="none"
      >
        {longClickDotBg}
        <path strokeWidth="10" stroke={defaultBg} fill="none" strokeLinejoin="round" strokeLinecap="round" d={path} />
        {arrowTransform && (
          <polygon strokeWidth="10" strokeLinecap="round" points="2,7 0,0 11,7 0,14" transform={arrowTransform} stroke={defaultBg} fill={defaultBg} />
        )}

        {longClickDot}
        <path strokeWidth="2" stroke={secondary} fill="none" strokeLinejoin="round" strokeLinecap="round" d={path} />
        {arrowTransform && (
          <polygon strokeWidth="2" strokeLinecap="round" points="2,7 0,0 11,7 0,14" transform={arrowTransform} stroke={secondary} fill={secondary} />
        )}
      </svg>
    </div>
  )
}
