import { Chip, Tooltip } from '@material-ui/core'
import { bg2 } from '../../../styles/colors'

interface Props {
  name: string
}

function TagWidget(props: Props) {
  const { name } = props

  const tag = window.tagsMapping.find((t: any) => t.name === name)
  const style = { backgroundColor: tag?.color || bg2, marginLeft: 8, height: 20 }
  const chip = <Chip size="small" label={name} style={style} />

  return (
    <span>
      {/** @ts-ignore */}
      <Tooltip title={tag?.description || ''}>{chip}</Tooltip>
    </span>
  )
}

export default TagWidget
