import React from 'react'
import { superLight } from '../../../../styles/colors'

interface Props {
  height?: string
  top?: string
  left?: string
  showTip?: boolean
}

export const VerticalLine = (props: Props) => {
  const { height, showTip = true } = props
  return (
    <div
      className="vl"
      style={{
        borderRight: `2px solid ${superLight}`,
        height: height || '30px'
        // marginTop: '5px'
      }}
    >
      {showTip && (
        <div
          className="point"
          style={{
            position: 'absolute',
            borderTop: `12px solid ${superLight}`,
            borderRight: '8px solid transparent',
            borderLeft: '8px solid transparent',
            marginLeft: '-7px',
            top: '100%'
          }}
        ></div>
      )}
    </div>
  )
}
