import { ArrowBack } from '@material-ui/icons'
import { secondary, transparentWhite } from '../../../../styles/colors'
import { Step } from '../../types'

interface Props {
  step: Step
}

export const AtoBline = ({ step }: Props) => {
  const p1 = step.startPositionSource.point
  const p2 = step.endPositionSource.point
  const centerX = (p1.x + p2.x) / 2
  const centerY = (p1.y + p2.y) / 2
  const dx = p1.x - p2.x
  const dy = p1.y - p2.y
  const deg = (Math.atan2(dy, dx) * 180) / Math.PI

  return (
    <ArrowBack
      style={{
        position: 'absolute',
        backgroundColor: transparentWhite,
        color: secondary,
        zIndex: 101,
        top: `calc(${centerY * 100}% - 35px)`,
        left: `calc(${centerX * 100}% - 35px)`,
        width: 70,
        height: 70,
        padding: 20,
        borderRadius: 35,
        transform: `rotate(${deg}deg)`
      }}
    />
  )
}
