/* eslint-disable jsx-a11y/alt-text */
// @ts-check
import { Avatar, CardHeader, Typography } from '@mui/material'
import React from 'react'
import { BatchRunExportDataDirName } from '../../../config'
import { errorGradient, errorMedium, errorWarm, success, successFont, successGradient, superLight, turmeric } from '../../../styles/colors'
import { Step, StepResult, Test, TestRun } from '../types'
import { formatDateTime, formatUserfacingTestId as formatId, getStepInfo } from '../../../lib/StringUtils'
import TagWidget from './TagWidget'
import { ImageMatchView } from './ImageMatchView'
import { VerticalLine } from './common'

interface Props {
  test: Test
  testRun: TestRun
  refTestRun: TestRun | undefined
  index: number
  step: Step
  stepResult: StepResult
  referenceSResult: StepResult | null
  refList: React.RefObject<HTMLImageElement>[]
  failStepRef: React.MutableRefObject<null>
  baseSrc?: string // contains absolute path in case of repeato studio
}

const stepType = {
  fontSize: '0.7rem',
  color: 'rgba(0, 0, 0, 0.6)'
}
const stepImageIcon: React.CSSProperties = {
  width: 'auto',
  maxHeight: '3rem'
  // margin: '11px'
}

const hBox: any = (org = true) => {
  return {
    position: 'absolute',
    bottom: 0,
    textAlign: 'center',
    width: '100%',
    borderRadius: '0 0 4px 4px'
  }
}

const lBox: any = (org = true) => {
  return {
    position: 'absolute',
    width: '600px',
    bottom: org ? '200px' : '0'
  }
}
const formatPercentage = (value: string) => {
  const valueNum = parseFloat(value)
  return `${(valueNum * 100).toFixed(2)}%`
}

const pStep = (step: Step, stepName: string) => (
  <React.Fragment>
    <span>{stepName}</span>
    {step.type !== 'comment' && <span style={stepType}>{` ID: ${formatId(step?.id)}`}</span>}
  </React.Fragment>
)

const stepImage = (test: Test, name: string, style: React.CSSProperties) => (
  <React.Fragment>
    <img style={style} src={name} alt={test.id} />
  </React.Fragment>
)

// arrows to point out towards the next step
const getArrows = (testRun: TestRun, index: number) => {
  const nextStep = testRun.stepResults[index]
  if (!nextStep) return
  // const step = stepResults[index - 1]
  return <VerticalLine height="30px" />
}

export const StepsItem = (props: Props) => {
  const { test, testRun, index, step, refList, stepResult, referenceSResult, failStepRef, refTestRun, baseSrc: bSrc } = props
  const [isDiffBatchHovered, setDiffBatchHovered] = React.useState(false)
  const [isDiffViewHovered, setDiffViewHovered] = React.useState(false)
  const baseSrc = bSrc ? `file://${bSrc}` : `./${BatchRunExportDataDirName}/${test.id}`
  const trBaseSrc = `testRuns/${testRun.id}`

  const stepName = getStepInfo(step as Step, 'all', false)
  const isSuccess = stepResult?.success
  const ref: React.RefObject<HTMLImageElement> = React.createRef()
  const isCopy = stepResult?.copy
  const border = `0px solid ${!isSuccess ? errorWarm : turmeric}`
  const similarityWarnThreshold = Number(test?.similarityWarnThreshold || '0.00')
  console.log('TestRunDevices', JSON.stringify(testRun), JSON.stringify(stepResult))
  const deviceData = testRun.devices.find(d => d.serial === stepResult.executedOn)
  const executionSkipped = step.type === 'comment' // we don't excute for comment step
  let isLandscape =
    deviceData?.currentRotation === deviceData?.rotationVars.landscape || deviceData?.currentRotation === deviceData?.rotationVars.seascape
  isLandscape = !deviceData ? false : isLandscape // in case of switch step we might not have deviceData if device was not attached then assume false
  console.log('deviceData', JSON.stringify(deviceData), isLandscape)

  refList.push(ref)

  let orgFrameSrc = step?.originalFrame && `${baseSrc}/${step?.originalFrame}`
  if (referenceSResult) {
    orgFrameSrc = `${baseSrc}/${`testRuns/${refTestRun?.id}/` + referenceSResult.screenshot}`
  }

  const screenshotSrc = stepResult?.screenshot && `${baseSrc}/${trBaseSrc}/${stepResult?.screenshot}`

  // data to show on image hover
  const stepHoverDetail = (type: string, isLandscape = true) => {
    const isOriginal = type === 'org'
    const stepColor = stepResult?.success ? success : errorWarm // success in case if test passed
    const backgroundColor = isOriginal ? success : stepColor // original screenshot will always be success
    const lStyleType = isOriginal ? !!stepResult?.screenshot : false // landscape box positioning if testRun screenshot exist
    const hStyleType = isOriginal ? true : !step?.originalFrame // horizontal box left right prop if original screenshot exist
    const text = isOriginal ? (referenceSResult ? 'Reference Test Run' : 'Original') : 'Test Run'
    const date =
      isOriginal && !referenceSResult ? step?.createdOn : referenceSResult && isOriginal ? referenceSResult.createdOn : stepResult?.createdOn
    const boxStyle = isLandscape ? lBox(lStyleType) : hBox(hStyleType) // horizontal box or landscape box
    // const typoStyle = isLandscape ? lTypoStyle : hTypoStyle // horizontal typo or landscape

    return (
      <div className="info" style={{ ...boxStyle, backgroundColor, paddingTop: 10, height: 55 }}>
        <Typography variant="body1">{text} Screenshot</Typography>
        <Typography variant="body2">{formatDateTime(new Date(date))}</Typography>
      </div>
    )
  }

  const renderResult = (src: string | undefined, type: string) => {
    //const { roi } = calculateRoi(step as Step)
    if (!src) return placeHolder(type)

    let matches = type === 'org' ? step?.matches : stepResult?.matches
    if (referenceSResult) {
      matches = referenceSResult.matches
    }

    return (
      <div className="matchResult" style={{ flexBasis: '50%', position: 'relative' }}>
        <ImageMatchView
          matchIndex={step?.matchIndex}
          matches={matches}
          selection={step?.selection}
          roi={step?.region}
          width={200}
          src={src}
          step={step}
        />

        {stepHoverDetail(type, false)}
      </div>
    )
  }

  const placeHolder = (type: any) => {
    return (
      <div
        style={{
          display: 'flex',
          justifyContent: 'center',
          alignItems: 'center',
          flexGrow: 1,
          position: 'relative',
          backgroundColor: superLight,
          borderRadius: 4,
          minHeight: 300
        }}
      >
        <Typography>Not available</Typography>
        {stepHoverDetail(type, false)}
      </div>
    )
  }

  const MaxSize = 600

  return (
    <>
      <div
        className="stepWrapper"
        ref={!isCopy && !isSuccess ? failStepRef : null}
        style={{
          minWidth: MaxSize,
          maxWidth: MaxSize,
          border: isCopy ? '' : border
        }}
      >
        <CardHeader
          avatar={
            (!executionSkipped && (
              <Avatar style={{ backgroundColor: isSuccess ? success : errorWarm }} aria-label="step-no">
                {stepName.icon && <stepName.icon />}
              </Avatar>
            )) || <p></p>
          }
          title={pStep(step as Step, stepName.primary)}
          subheader={
            <>
              <span style={{ whiteSpace: 'pre-wrap', overflow: 'hidden' }}>{stepName.secondary}</span>
              {step?.tags?.map((tag: any, index: number) => (
                <TagWidget name={tag} key={index} />
              ))}
            </>
          }
          action={step?.image && <>{stepImage(test, `${baseSrc}/${step?.image}`, stepImageIcon)}</>}
          style={{}}
        />

        <div
          style={{
            borderBottom: '2px solid ' + superLight
          }}
        >
          <div
            style={{
              position: 'relative',

              paddingBottom: 20
            }}
          >
            {!executionSkipped && (
              <div style={{ display: 'flex', flexDirection: isLandscape ? 'column' : 'row' }}>
                {renderResult(orgFrameSrc, 'org')}
                <div style={{ height: 10, width: 10 }}></div>
                {renderResult(screenshotSrc, 'testRun')}
              </div>
            )}

            {stepResult?.screenSimilarity && (
              <>
                <div style={{ position: 'absolute', bottom: 0, width: '100%', height: 100, overflow: 'hidden' }}>
                  <div
                    className={`similarityBatch ${Number(stepResult?.screenSimilarity) > similarityWarnThreshold ? 'above' : 'below'}`}
                    onMouseEnter={event => setDiffBatchHovered(true)}
                    onMouseLeave={event => setDiffBatchHovered(false)}
                  >
                    <Typography variant="body1">{formatPercentage(stepResult?.screenSimilarity)}</Typography>
                    <Typography variant="body2">Screen Similarity</Typography>
                  </div>
                </div>
                {(isDiffBatchHovered || isDiffViewHovered) && orgFrameSrc && screenshotSrc && (
                  <div
                    onMouseEnter={event => setDiffViewHovered(true)}
                    onMouseLeave={event => setDiffViewHovered(false)}
                    className="diffImg"
                    style={{
                      width: MaxSize,
                      height: MaxSize,
                      position: 'absolute',
                      justifyContent: 'center',
                      alignItems: 'center',
                      display: 'flex',
                      backgroundColor: '#FFF',
                      boxShadow: '0px 2px 28px #00000057',
                      borderRadius: 5,
                      top: 0
                    }}
                  >
                    <img
                      src={orgFrameSrc}
                      style={{ position: 'absolute', maxHeight: MaxSize, maxWidth: MaxSize, boxShadow: '0px 2px 28px #00000057' }}
                    />
                    <img src={screenshotSrc} style={{ position: 'absolute', maxHeight: MaxSize, maxWidth: MaxSize, mixBlendMode: 'difference' }} />
                  </div>
                )}
              </>
            )}
          </div>
          {stepResult?.message && (
            <div
              style={{
                borderRadius: 4,
                padding: 10,
                background: isSuccess ? successGradient : errorGradient,
                marginBottom: 20
              }}
            >
              <Typography variant="body2" sx={{ color: isSuccess ? successFont : errorMedium, whiteSpace: 'pre-wrap', fontFamily: 'monospace' }}>
                {stepResult?.message}
              </Typography>
            </div>
          )}
        </div>

        {/* <CardActions disableSpacing></CardActions> */}
      </div>

      {getArrows(testRun, index + 1)}
    </>
  )
}
