import { Typography } from '@mui/material'
import { darkBg2 } from '../../../styles/colors'

interface Props {
  name?: String
  value: String | number
  className?: string
}

const lineStyle = '1px solid ' + darkBg2

const separatorStyle = {
  borderBottom: lineStyle,
  marginBottom: 8,
  paddingBottom: 6
}

const h3Style = { fontSize: '0.8rem', fontWeight: 400, color: darkBg2, marginBottom: 3 }

export const NameValue = (props: Props) => {
  const { name, value, className } = props

  let val
  if (!Boolean(value) && '' + value !== '0') {
    val = 'Not available'
  } else {
    val = value
  }
  return (
    <div style={separatorStyle} className={className || ''}>
      {name && (
        <Typography variant="h3" style={h3Style}>
          {name}
        </Typography>
      )}
      <Typography variant="h2" style={{ fontSize: '1.0rem' }}>
        {val}
      </Typography>
    </div>
  )
}
