import React from 'react'
import { IconButton } from '@mui/material'
import { StepsInfo, TestInfo } from './'
import { Test } from '../types'
import { Close } from '@material-ui/icons'
import { transparentWhite } from '../../../styles/colors'
import { useNavigate, useParams } from 'react-router'

interface Props {
  setActiveTab: React.Dispatch<React.SetStateAction<number | null>>
}

export const TestView = ({ setActiveTab }: Props) => {
  const { testId, testIndex = 0 } = useParams()
  const navigate = useNavigate()

  const tests = batchRun.tests || []
  const failStepRef = React.useRef(null)
  const [test, setTest] = React.useState<Test | undefined>(undefined)

  React.useEffect(() => setActiveTab(Number(testIndex)), [])

  // we are not using testIndex to filter anymore because index can be changed when perform searching/filters
  // testIds can be same if test is added multiple times in batch but for now report get overwritten by last testRun if test is same
  // so for now filtering by test id works fine, but in case more support is added for same tests
  // then we might introduce testRun date or any other unique field to identify tests uniquely
  React.useEffect(() => setTest(tests.find(t => t.id === testId) as any), [testId]) // eslint-disable-line react-hooks/exhaustive-deps

  return (
    <>
      <IconButton
        id="closeButton"
        style={{ position: 'fixed', right: 30, top: 40, backgroundColor: transparentWhite, zIndex: 100 }}
        onClick={() => {
          setActiveTab(null)
          navigate('/')
        }}
      >
        <Close />
      </IconButton>

      {test && (
        <div id="scrollContainer" style={{ flexGrow: 1, overflow: 'scroll', padding: 40 }}>
          <TestInfo test={test} failStepRef={failStepRef} />
          <StepsInfo test={test} failStepRef={failStepRef} />
        </div>
      )}
      {!test && <p>Test not found!</p>}
    </>
  )
}
