import { useRef } from 'react'
import { CenterFocusWeak } from '@material-ui/icons'
import { secondary, invertedFont } from '../../../../styles/colors'

export const PositionSpot = (props: any) => {
  const positionSource = props.positionSource
  const point = positionSource.point
  const containerRef = useRef(null)
  const ref = useRef(null)

  const x = point.x * 100 + '%'
  const y = point.y * 100 + '%'

  return (
    <div
      ref={containerRef}
      style={{
        position: 'relative',
        height: '200px',
        width: '200px',
        backgroundColor: 'rgba(100,100,100)',
        ...props.style
      }}
    >
      {props.children}
      <CenterFocusWeak
        ref={ref}
        style={{
          color: invertedFont,
          position: 'absolute',
          width: 10,
          height: 10,
          padding: 20,
          borderRadius: 35,
          left: `calc(${x} - 5px)`,
          top: `calc(${y} - 5px)`,
          cursor: 'grabbing',
          zIndex: 101,
          backgroundColor: secondary
        }}
      />
    </div>
  )
}
